.home
  .navbar-nav
    &__dark
      li
        &:last-child
          border-color: $main-color !important
          &::before
            background-color: $main-color !important
      .menu-item
        .nav-link
          color: $main-color
          &:hover
            color: #DEE3E4
      .current-menu-item
        .nav-link
          color: #DEE3E4

  
#main-header
  .navbar
    .site-branding
      .site-logo
        svg
          max-width: 40px
          height: auto
          fill: $dark-color
    .navbar-actions
      overflow: hidden
    &__light, &__dark
      .site-branding
          .site-logo
            path
              fill: $main-color !important
  
.navbar-nav
  li
    &:last-child
      border-color: #dee3e4
      &::before
        background-color: $text-color !important
  .menu-item
    .nav-link
      font-family: $main-font
      font-size: 16px
      font-weight: 400
      line-height: 1
      color: $dark-color
      text-decoration: none
      &:hover
        color: #DEE3E4
  .current-menu-item
    .nav-link
      color: #DEE3E4

  &__light
    li
      &:last-child
        border-color: $text-color
      &::before
        background-color: $text-color !important
    .menu-item
      .nav-link
        color: #445659
        &:hover
          color: $main-color
    .current-menu-item
      .nav-link
        color: $main-color
  &__dark
    li
      &:last-child
        border-color: #dee3e4 !important
        &::before
          background-color: #dee3e4 !important
    .menu-item
      .nav-link
        color: #DEE3E4
        &:hover
          color: $main-color
    .current-menu-item
      .nav-link
        color: $main-color
  .dropdown-menu
    background-color: transparent
    border: 0
    padding: 0 10px
    top: 25px
    li
      a
        font-family: $main-font
        font-size: 16px
        font-weight: 400
        color: $main-color
        &:hover
          background-color: transparent
          color: $bg-color

@include media-breakpoint-up(lg)
  #main-header
    .navbar
      transition: all .4s ease-in-out
      padding-top: 35px
      padding-bottom: 40px
    .navbar-collapse
      justify-content: flex-end
    .navbar-nav
      .nav-link
        padding-right: 1.6rem
        padding-left: 1.6rem
    .menu-button
      &__start
        .nav-link
          background-color: $accent-color
          font-size: 12px !important
          font-weight: 400
          line-height: 16px
          text-transform: uppercase
          color: #F7F7F7 !important
          border-radius: 30px
          padding: 1.1rem 3rem 0.9rem !important
          margin-left: 3rem
          box-shadow: 0 5px 2rem rgb(0 0 0 / 18%) !important
          transition: all .3s ease-in
          &:hover
            background-color: $dark-color
            box-shadow: 0 2px 2px rgb(0 0 0 / 18%) !important
  
  .wpml-ls-legacy-list-horizontal
    display: none

  .navbar-nav
    li
      &:last-child
        position: relative
        &::before
          content: ""
          position: absolute
          background-color: $main-color
          width: 1px
          height: 14px
          bottom: 0
          left: 0
        a
          padding: 0 1.6rem 0 0.8rem !important
      &:nth-last-child(2)
        a
          padding: 0 0.8rem 0 1.6rem !important
        

  #main-header.sticky
    .dropdown-menu
      li
        a
          color: $text-color
          &:hover
            background-color: transparent
            color: $main-color
    
    .home
      .navbar-nav
        &__dark
          li
            &:last-child
              border-color: $text-color !important
              &::before
                background-color: $text-color !important
    .navbar
      padding-top: 20px
      padding-bottom: 20px
      background-color: $bg-color
      box-shadow: 0px -1px 4px 0px rgb(0 0 0 / 20%)
      .site-branding
        .site-logo
          path
            fill: $main-color !important
      .nav-link
        color: #445659
        &:hover
          color: $main-color
      .current-menu-item
        .nav-link
          color: $main-color
      &__light
        background-color: $dark-color
        li
          &:last-child
            border-color: #dee3e4
            &::before
              background-color: #dee3e4 !important
        .nav-link
          color: #DEE3E4
          &:hover
            color: $main-color
        .current-menu-item
          .nav-link
            color: $main-color
      &__dark
        background-color: $bg-color
        li
          &:last-child
            border-color: $text-color !important
            &::before
              background-color: $text-color !important
        .nav-link
          color: #445659
          &:hover
            color: $main-color
        .current-menu-item
          .nav-link
            color: $main-color
      .navbar-actions
        .navbar-toggler
          .icon-left,
          .icon-right
            background-color: #fff
            &::before,
            &::after
              background-color: $main-color
            &:not(.collapsed)
              background-color: $main-color

@include media-breakpoint-between(lg, xl)
  #main-header
    .navbar-nav
      .nav-link
        padding-right: 1.2rem
        padding-left: 1.2rem


@include media-breakpoint-down(lg)
  .wpml-ls-menu-item
    display: none
  .wpml-ls-legacy-list-horizontal
    position: absolute
    bottom: 34%
    left: 50%
    transform: translateX(-50%)
    li
      &:first-child
        position: relative
        &::after
          content: ""
          position: absolute
          background-color: $main-color
          width: 1px
          height: 80%
          bottom: 0
          right: 0
      a
        color: $main-color
        padding: 0 10px
        span
          font-size: 32px
          color: $main-color
          font-weight: 300

  .animated-icon2
    width: 30px
    height: 20px
    position: relative
    margin: 0px
    -webkit-transform: rotate(0deg)
    -moz-transform: rotate(0deg)
    -o-transform: rotate(0deg)
    transform: rotate(0deg)
    -webkit-transition: .5s ease-in-out
    -moz-transition: .5s ease-in-out
    -o-transition: .5s ease-in-out
    transition: .5s ease-in-out
    cursor: pointer
    span
      background: $main-color
      display: block
      position: absolute
      height: 3px
      width: 100%
      border-radius: 9px
      opacity: 1
      left: 0
      -webkit-transform: rotate(0deg)
      -moz-transform: rotate(0deg)
      -o-transform: rotate(0deg)
      transform: rotate(0deg)
      -webkit-transition: .25s ease-in-out
      -moz-transition: .25s ease-in-out
      -o-transition: .25s ease-in-out
      transition: .25s ease-in-out
      &:nth-child(1)
        top: 0px
      &:nth-child(2)
        top: 10px
      &:nth-child(3)
        top: 10px
      &:nth-child(4)
        top: 20px
  .animated-icon2.open
    span
      background: $main-color
      &:nth-child(1)
        top: 11px
        width: 0%
        left: 50%
      &:nth-child(2)
        -webkit-transform: rotate(45deg)
        -moz-transform: rotate(45deg)
        -o-transform: rotate(45deg)
        transform: rotate(45deg)
      &:nth-child(3)
        -webkit-transform: rotate(-45deg)
        -moz-transform: rotate(-45deg)
        -o-transform: rotate(-45deg)
        transform: rotate(-45deg)
      &:nth-child(4)
        top: 11px
        width: 0%
        left: 50%
  .page-template-page-starting-project
    .animated-icon2
      span
        background: $dark-color
    .animated-icon2.open
      span
        background: $main-color

  #main-header
    .navbar
      padding: 40px 15px 30px
      background-color: transparent
      transition: all 300ms ease-in-out
      .navbar-collapse
          padding: 30px 0
      &.nav-open
        background-color: $dark-color
        .navbar-toggler-icon
          /*color: white*/
        .site-branding
          .site-logo
            svg
              fill: $main-color
      .site-branding
        .navbar-brand
          display: block
          .custom-logo
            width: 100%
            object-fit: cover
            transition: all 600ms ease-in-out
            path
              transition: all 600ms ease-in-out
    .navbar-nav
      position: relative
      min-height: calc(100vh - 118px)
      align-items: flex-end !important
      .nav-link
        color: $main-color
        text-align: right
        font-size: 32px
        font-weight: 300
        margin-bottom: 20px
    .navbar-actions
      display: flex
      align-items: center
      .navbar-toggler
        position: relative
        width: 30px
        height: 30px
        padding: 0
        transition-duration: 0.5s
        outline: none
        box-shadow: none
        border: 0
        &:focus,
        &:active
          outline: none
          box-shadow: none
          border: 0
        &:not(.collapsed)
          .icon-left
            transition-duration: 0.5s
            background-color: transparent
            &::before
              transform: rotateZ(45deg) scaleX(1.4) translate(1px, 1px)
            &::after
              transform: rotateZ(-45deg) scaleX(1.4) translate(1px, -1px)
          .icon-right
            transition-duration: 0.5s
            background-color: transparent
            &::before
              transform: rotateZ(-45deg) scaleX(1.4) translate(-1px, 1px)
            &::after
              transform: rotateZ(45deg) scaleX(1.4) translate(-1px, -1px)
    .menu-button
      &__start
        position: absolute
        bottom: 75px
        width: 100%
        text-align: center
        .nav-link
          display: inline-block
          background-color: $accent-color
          font-size: 12px !important
          font-weight: 400
          line-height: 16px
          text-transform: uppercase
          color: #F7F7F7 !important
          border-radius: 30px
          padding: 1.1rem 3rem 0.9rem !important
          margin-top: 1rem
          box-shadow: 0 1rem 3rem rgb(0 0 0 / 18%) !important

  #main-header.sticky
    .navbar
      background-color: $bg-color
      box-shadow: 0px -1px 4px 0px rgb(0 0 0 / 20%)
      padding-top: 25px
      padding-bottom: 25px
      &.nav-open
        background-color: $dark-color

@include media-breakpoint-down(sm)
  #main-header
    .navbar-nav
      .nav-link
        font-size: 26px

  .wpml-ls-legacy-list-horizontal
    li
      a
        span
          font-size: 26px